<script>
    import lottie from 'lottie-web';
    import {TweenMax, Power0, Power1, Power2} from 'gsap';
    import connectedJson from '@/assets/lottie/connected.json';
    import connectedArrowJson from '@/assets/lottie/connected-arrow.json';

    export default {
        name: 'HomePreviewSession',
        data() {
            return {
                iconList: [
                    {
                        name: 'whatsapp',
                        img: require(`../assets/home/icon-whatsapp.svg`),
                    },
                    {
                        name: 'gmail',
                        img: require(`../assets/home/icon-gmail.svg`),
                    },
                    {
                        name: 'netflix',
                        img: require(`../assets/home/icon-netflix.svg`),
                    },
                    {
                        name: 'telegram',
                        img: require(`../assets/home/icon-telegram.svg`),
                    },
                    // {
                    //   img: require('../assets/home/icon-hulu.svg'),
                    // },
                    {
                        name: 'youtube',
                        img: require(`../assets/home/icon-youtube.svg`),
                    },
                    {
                        name: 'facebook',
                        img: require(`../assets/home/icon-facebook.svg`),
                    },
                    {
                        name: 'ins',
                        img: require(`../assets/home/icon-ins.svg`),
                    },
                    {
                        name: 'line',
                        img: require(`../assets/home/icon-line.svg`),
                    },
                    {
                        name: 'twitter',
                        img: require(`../assets/home/icon-twitter.svg`),
                    },
                ],
                iconListCL: [
                    {
                        name: 'whatsapp',
                        img: require(`../assets/home/icon-whatsapp-cl.png`),
                    },
                    {
                        name: 'gmail',
                        img: require(`../assets/home/icon-gmail-cl.png`),
                    },
                    {
                        name: 'netflix',
                        img: require(`../assets/home/icon-netflix-cl.png`),
                    },
                    {
                        name: 'telegram',
                        img: require(`../assets/home/icon-telegram-cl.png`),
                    },
                    // {
                    //   img: require('../assets/home/icon-hulu.svg'),
                    // },
                    {
                        name: 'youtube',
                        img: require(`../assets/home/icon-youtube-cl.png`),
                    },
                    {
                        name: 'facebook',
                        img: require(`../assets/home/icon-facebook-cl.png`),
                    },
                    {
                        name: 'ins',
                        img: require(`../assets/home/icon-ins-cl.png`),
                    },
                    {
                        name: 'line',
                        img: require(`../assets/home/icon-line-cl.png`),
                    },
                    {
                        name: 'twitter',
                        img: require(`../assets/home/icon-twitter-cl.png`),
                    },
                ],
                hideConnectBG: false,
                connectLoading: true,
                clickTime: 0.13,
                animation1: [],
                animation2: [],
                test: false,
                isPc: this.$isPc(),
                ct: 1,
                cl: 1
            };
        },
        props: {
            isShowAnimation: {},
            isCT: {},
            isCL: {}
        },
        watch: {
            isShowAnimation: {
                immediate: true,    // 
                handler(val) {
                    if (!this.animation1 || !this.animation2 || !this.test) {
                        return;
                    }

                    if (val) {
                        Array.prototype.forEach.call(this.animation1, function (el) {
                            el.resume();
                        });
                        Array.prototype.forEach.call(this.animation2, function (el) {
                            el.resume();
                        });
                    } else {
                        Array.prototype.forEach.call(this.animation1, function (el) {
                            el.pause();
                        });
                        Array.prototype.forEach.call(this.animation2, function (el) {
                            el.pause();
                        });
                    }
                }
            },
            isCT: {
                immediate: true,
                handler(val) {
                    if (val !== undefined) {
                        this.ct = val
                    }
                }
            },
            isCL: {
                immediate: true,
                handler(val) {
                    if (val !== undefined) {
                        this.cl = val
                    }
                }
            }
        },
        mounted() {
            this.goConnect()
        },
        methods: {
            goToPolicyDetail() {
                window.open(`${window.location.origin}/help/article/360004852651`, this.$inApp ? '_self' : '_blank');
            },
            gotoOrder() {
                gtag('event', 'try_now_firstBtn')
                window.open(`${window.location.origin}/order/place`, this.$inApp ? '_self' : '_blank');
            },
            goConnect() {
                const that = this;
                TweenMax.fromTo(
                    document.getElementById('connect-stand-by'),
                    that.clickTime,
                    {transform: 'scale(1)'},
                    {
                        transform: 'scale(0.9)',
                        delay: 2,
                        onComplete: function () {
                            TweenMax.fromTo(
                                document.getElementById('connect-stand-by'),
                                that.clickTime,
                                {transform: 'scale(0.9)'},
                                {transform: 'scale(1)'}
                            );
                        }
                    }
                );

                TweenMax.fromTo(
                    document.getElementById('connect-stand-by-by'),
                    that.clickTime,
                    {
                        transform: 'scale(1)',
                        opacity: 0
                    },
                    {
                        transform: 'scale(0.9)',
                        opacity: 0.3,
                        delay: 2,
                        onComplete: function () {
                            TweenMax.fromTo(
                                document.getElementById('connect-stand-by-by'),
                                that.clickTime,
                                {
                                    transform: 'scale(0.9)',
                                    opacity: 0.3
                                },
                                {
                                    transform: 'scale(1)',
                                    opacity: 0,
                                    onComplete: function () {
                                        that.connectLoading = false;
                                        TweenMax.fromTo(
                                            document.getElementById('connect-loading'),
                                            3,
                                            {
                                                rotationZ: 0,
                                                rotationX: 19
                                            },
                                            {
                                                rotationZ: -(360 * 3),
                                                rotationX: 19,
                                                ease: Power2.easeInOut,
                                                onComplete: function () {
                                                    that.lottieStart();
                                                }
                                            });
                                    }
                                }
                            );
                        }
                    }
                );
            },
            lottieStart() {
                this.connectLoading = true;
                lottie.loadAnimation({
                    wrapper: document.getElementById('lottie-centre'),
                    animType: 'svg',
                    loop: true,
                    animationData: connectedJson
                });

                this.hideConnectBG = true;

                lottie.loadAnimation({
                    wrapper: document.getElementById('lottie-centre-arrow'),
                    animType: 'svg',
                    loop: false,
                    animationData: connectedArrowJson
                });


                let that = this;
                Array.prototype.forEach.call(document.getElementsByClassName('move-block'), function (el, index) {
                    TweenMax.set(el, {opacity: 1})
                    TweenMax.fromTo(el, 1.5, {rotation: 0}, {
                        css: {rotation: (((index + 1) * 360) / that.iconList.length)},
                        delay: (index * 0.02),
                        ease: Power1.easeOut,
                        onComplete: function () {
                            that.test = true;
                            that.animation1[index] = TweenMax.fromTo(el, 24, {rotation: (((index + 1) * 360) / that.iconList.length)}, {
                                css: {rotation: (((index + 1) * 360) / that.iconList.length) + 360},
                                repeat: -1,
                                ease: Power0.easeNone
                            });
                            that.animation2[index] = TweenMax.fromTo(el.getElementsByClassName('move-icon')[0], 24, {rotation: -(((index + 1) * 360) / that.iconList.length)}, {
                                css: {rotation: -(((index + 1) * 360) / that.iconList.length) - 360},
                                repeat: -1,
                                ease: Power0.easeNone
                            });
                        },
                        onStart: function () {
                            TweenMax.fromTo(el.getElementsByClassName('move-icon')[0], 1.5, {rotation: 0}, {
                                css: {rotation: -(((index + 1) * 360) / that.iconList.length)},
                                ease: Power1.easeOut
                            });
                        }
                    });
                });


            }
        }
    };
</script>

