                            require(`../assets/service/ios-surge-icon.svg`),
                        ],
                        isSelected: false
                    },
                    {
                        title: 'home:screen10.tab-4',
                        description: ['home:screen10.tab-4-line-1', 'home:screen10.tab-4-line-2'],
                        icons: [
                            require(`../assets/service/auVPN-icon.svg`),
                            require(`../assets/service/quantumult-icon.svg`),
