                            require(`../assets/service/windows-clash-icon.svg`),
                            require(`../assets/service/shadowrocket-icon.svg`),
                        ],
                        isSelected: false
                    },
                    {
                        title: 'home:screen10.tab-5',
                        description: ['home:screen10.tab-5-line-1', 'home:screen10.tab-5-line-2'],
                        icons: [
                            require(`../assets/service/v2rayn-icon.svg`),
                            require(`../assets/service/winxray-icon.svg`),
                        ],
                        isSelected: false
                    },
                    {
                        title: 'home:screen10.tab-6',
                        description: ['home:screen10.tab-6-line-1', 'home:screen10.tab-6-line-2'],
                        icons: [
                            require(`../assets/service/wireguard-icon.svg`),
                        ],
                        isSelected: false
                    }
                ],
                selectedItem: {
                    title: 'home:screen10.tab-1',
                    description: ['home:screen10.tab-1-line-1',
                        'home:screen10.tab-1-line-2', 'home:screen10.tab-1-line-3'],
                    icons: [
                        require(`../assets/service/quantumult-icon.svg`),
                        require(`../assets/service/ios-surge-icon.svg`),
                        require(`../assets/service/shadowrocket-icon.svg`),
                        require(`../assets/service/windows-clash-icon.svg`)
                    ],
                    isSelected: true
                },
                isPc: this.$isPc(),
                selectedIndex: 0,
                systemIndex: 0
            };
        },
        methods: {
            selectedProtocol(item, selectedIndex) {
                this.techList.forEach((item, index) => {
                    item.isSelected = (index === selectedIndex);
                });
                this.selectedItem = item;
                this.scrollSystemItem(selectedIndex)

                this.systemIndex = selectedIndex
            },
            gotoOrder() {
                gtag('event', 'try_now_multiProtocol')
                window.open(`${window.location.origin}/order/place`, this.$inApp ? '_self' : '_blank');
            },
            // scroll system item to middle
            scrollSystemItem(index) {

                let systemItems = document.getElementsByClassName('protocol-name')
                let iconItems = document.getElementsByClassName('selected-protocol')
                if (systemItems.length > 0) {
                    // consider the down-icon only display when selected
                    const d = systemItems.item(index).offsetLeft //- (index > this.systemIndex ? iconItems.item(0).clientWidth : 0)
                    const c = systemItems.item(index).clientWidth //+ (index !== this.systemIndex ? iconItems.item(0).clientWidth : 0)
                    const b = document.body.clientWidth
                    // console.log(systemItems.item(index).getBoundingClientRect().left)

                    TweenMax.fromTo(
                        document.getElementById('protocolsList'),
                        0.3,
                        {scrollLeft: this.$refs.protocolsList.scrollLeft},
                        {
                            scrollLeft: d + (c) / 2 - b / 2,
                        },
                    );
                }
            }
        }
    };
</script>

