<template>
  <div :class="{'anonymity-radar-container': isPc, 'anonymity-radar-container-mb': !isPc}">
    <div class="radar-bg" v-if="isPc"></div>
    <i class="radar-circle"></i>
    <i class="radar-circle radar-circle-outside"></i>
    <i class="radar-scanner" id="radar-scanner"></i>
    <div class="masking-bg2" id="masking-bg2">
      <i class="masking-arr"></i>
    </div>
    <!--<div class="masking-bg" id="masking-bg">-->
    <!--<div class="masking-arr"></div>-->
    <!--</div>-->
    <div class="node-point-list">
      <RadarPoint class="radar-point" v-for="(item,index) in radarPointList"  :key="index"
                  :style='{top: item.top, left: item.left}'></RadarPoint>
    </div>
  </div>
</template>

<script>
  import RadarPoint from '@/components/RadarPoint.vue';
  import {TweenMax} from 'gsap';
  import {Power0} from 'gsap';
  export default {
    name: 'AnonymityRadarView',
    components: {
      RadarPoint
    },
    data() {
      return {
        radarPointList: [
          {
            top: '102px',
            left: '30px',
            lambda: 20,

          },
          {
            top: '17px',
            left: '129px',
            lambda: 20,

          },
          {
            top: '237px',
            left: '79.7px',
            lambda: 20,

          },
          {
            top: '97px',
            left: '204.7px',
            lambda: 20,

          },
          {
            top: '188px',
            left: '204.7px',
            lambda: 20,

          },
        ],
        radarPointListMb: [
          {
            top: '2.401rem',
            left: '0.802rem',
            lambda: 20,
          },
          {
            top: '0.474rem',
            left: '2.807rem',
            lambda: 6,
          },
          {
            top: '5.013rem',
            left: '1.604rem',
            lambda: 10,
          },
          {
            top: '2.14rem',
            left: '4.411rem',
            lambda: 16,
          },
          {
            top: '4.277rem',
            left: '4.143rem',
            lambda: 15,
          },
        ],
        isPc: this.$isPc(),
        maxDelay: 0.5,

      };
    },
    created() {
      if (!this.isPc) {
        this.radarPointList = this.radarPointListMb
      }
    },
    mounted() {
      TweenMax.to(
        document.getElementById('radar-scanner'),
        10,
        {css: {rotation: 360}, repeat: -1, ease: Power0.easeNone}
      )
      TweenMax.to(
        document.getElementById('masking-bg2'),
        10,
        {css: {rotation: 383}, repeat: -1, ease: Power0.easeNone}
      )

      let that = this;
      Array.prototype.forEach.call(document.getElementsByClassName('radar-point'), function (el, index) {
        let delayT = Math.random() * 3;
        // init delay for each point
        that.animateGo(delayT, el, that, index)
      });
    },
    methods: {
      animateGo(delayT, el, that, index) {
        TweenMax.fromTo(el.getElementsByClassName('pulse-radar')[0],
          (2 - (that.radarPointList[index].lambda/17)), // calculate the duration with lambda
          {
            transform: 'scale(0.3)',
            opacity: '1'
          }, {
            css: {
              transform: that.isPc?'scale(2)': 'scale(2.5)',
              opacity: '0'
            },
            delay: delayT,
            // repeat: that.isPc? 0: -1,
          });
        // set the second ring with different delay time(1/3 of prev)
        TweenMax.fromTo(el.getElementsByClassName('pulse1-radar')[0], (2 - (that.radarPointList[index].lambda/17)), {
          transform: 'scale(0.3)',
          opacity: '1'
        }, {
          css: {
            transform: that.isPc?'scale(2)': 'scale(2.5)',
            opacity: '0'
          },
          delay: delayT + (2 - (that.radarPointList[index].lambda/17))/3,
          // repeat: that.isPc? 0: -1,
          onComplete: function() {
            // if (that.isPc) {
            that.animateGo(that.getNextTime(that.radarPointList[index].lambda), el, that, index)
            // }
          }
        });
      },
      getNextTime(lambda) {
        // let time = Math.min(-Math.log(1 - Math.random())/(lambda/20), this.maxDelay)
        // console.log(`${lambda} --- ${time}`)
        // calculate the new delay time with Poisson
        return (Math.min(-Math.log(1 - Math.random()) / (lambda / 17), this.maxDelay));
      }
    }
  };
</script>

