<template>
    <div class="home-container" :class="{'home-container-pc':isPc}">
        <HomePreviewSession v-if="showFlagList[0]" v-bind:isShowAnimation="showHeader" v-bind:isCL="cl" v-bind:isCT="ct"
                            :style="{'z-index': 10}"></HomePreviewSession>
        <HomeFeatureMap v-if="showFlagList[1]" :style="{'z-index': 20}"></HomeFeatureMap>
        <HomeMultiPlatform v-if="showFlagList[2]" :style="{'z-index': 30}"></HomeMultiPlatform>
        <HomeThreeFeature v-if="showFlagList[3]" :style="{'z-index': 40}"></HomeThreeFeature>
        <HomeServiceMap v-if="showFlagList[4]" v-bind:isShowAnimation="showServiceMap"
                        :style="{'z-index': 50}"></HomeServiceMap>
        <HomeNetworkProvider v-if="showFlagList[5]" :style="{'z-index': 60}"></HomeNetworkProvider>
        <HomeOperationalData v-if="showFlagList[6]" v-bind:isShow="showOperationalData"
                             :style="{'z-index': 70}"></HomeOperationalData>
        <HomeProtocolsTech v-if="showFlagList[7]" :style="{'z-index': 80}"></HomeProtocolsTech>
        <HomeServerGuarantee v-if="showFlagList[8]" :style="{'z-index': 90}"></HomeServerGuarantee>
        <HomeCustomerReview v-if="showFlagList[9]" :style="{'z-index': 100}"
                            v-bind:isChangeLang="isChangeLang"></HomeCustomerReview>
        <HomeAppDownload v-if="showFlagList[10]" :style="{'z-index': 110}"></HomeAppDownload>
        <HomeQA v-if="showFlagList[11]" :style="{'z-index': 120}"></HomeQA>
        <HomeSevenDaysTry v-if="showFlagList[12]" :style="{'z-index': 130}"></HomeSevenDaysTry>
        <PublicFooter v-if="showFlagList[13]" :style="{'z-index': 140}"></PublicFooter>
        <i :class="{'back-to-top': isPc, 'back-to-top-mb': !isPc, 'back-to-top-mb-hidden': backToTopShow}"
           @click="backToTop()"></i>
    </div>
</template>

<script>
    import HomePreviewSession from '@/components/HomePreviewSession.vue';
    import HomeFeatureMap from '@/components/HomeFeatureMap.vue';
    import HomeMultiPlatform from '@/components/HomeMultiPlatform.vue';
    import HomeThreeFeature from '@/components/HomeThreeFeature.vue';
    import HomeServiceMap from '@/components/HomeServiceMap.vue';
    import HomeNetworkProvider from '@/components/HomeNetworkProvider.vue';
    import HomeOperationalData from '@/components/HomeOperationalData.vue';
    import HomeProtocolsTech from '@/components/HomeProtocolsTech.vue';
    import HomeServerGuarantee from '@/components/HomeServerGuarantee.vue';
    import HomeCustomerReview from '@/components/HomeCustomerReview.vue';
    import HomeAppDownload from '@/components/HomeAppDownload.vue';
    import HomeQA from '@/components/HomeQA.vue';
    import PublicFooter from '@/components/PublicFooter.vue';
    import HomeSevenDaysTry from '@/components/HomeSevenDaysTry.vue';
    import * as _ from 'lodash';
    import {AU_LOCALE_KEY} from "../i18n";

    export default {
        name: 'Home',
        components: {
            HomePreviewSession,
            HomeFeatureMap,
            HomeMultiPlatform,
            HomeThreeFeature,
            HomeServiceMap,
            HomeNetworkProvider,
            HomeOperationalData,
            HomeProtocolsTech,
            HomeServerGuarantee,
            HomeCustomerReview,
            HomeAppDownload,
            HomeQA,
            HomeSevenDaysTry,
            PublicFooter,
        },
        data() {
            return {
                itemList: Array,
                showOperationalData: false,
                showServiceMap: false,
                showHeader: true,
                isPc: this.$isPc(),
                backToTopShow: true,
                showFlagList: [],
                showFlagLength: 14,
                lazyLoading: false,
                isChangeLang: false,
                ct: 1,
                cl: 1,
                eventInterval: null,
                eventIntervalCount: 0,
                isShowing: false
            };
        },
        created() {
            this.itemList = document.getElementsByClassName('content');
            this.showFlagList = new Array(this.showFlagLength).fill(false);
            this.showFlagList[0] = true;
            this.showFlagList[1] = true;
            if (this.$route.query.ct)
            {
                this.ct = Number(this.$route.query.ct)
            }
            if (this.$route.query.cl) {
                this.cl = Number(this.$route.query.cl)
            }
        },
        mounted() {
            const that = this
            this.isShowing = document.visibilityState == "visible"

            document.addEventListener("visibilitychange", function() {
                that.isShowing = document.visibilityState == "visible"
            });


            // Back to the top
            window.onbeforeunload = function () {
                //
                document.documentElement.scrollTop = 0;  //ie
                document.body.scrollTop = 0;  //ie
            }
            setTimeout(function () {
                that.showFlagList = new Array(that.showFlagLength).fill(true)
            }, 1000)
            window.addEventListener('scroll', this.handleScroll);
            this.handleScroll();

            // setup the lang for ct
            let lang = window.localStorage.getItem(AU_LOCALE_KEY);
            if (this.ct === 1) {
                this.$setupLang(`${lang}-ct`)
            } else {
                this.$setupLang(lang)
            }

            // event fire
            this.$eventFire.viewContent()
            this.eventInterval = setInterval(function () {
                if (that.isShowing){
                    // calculate only the page isShowing
                    if (that.eventIntervalCount > 20) {
                        window.clearInterval(that.eventInterval)
                    } else {
                        switch (that.eventIntervalCount) {
                            case 5:
                                that.$eventFire.landingPageEngage5s()
                                break;
                            case 10:
                                that.$eventFire.landingPageEngage10s()
                                break;
                            case 20:
                                that.$eventFire.landingPageEngage20s()
                                break
                        }
                        that.eventIntervalCount ++
                    }
                }
            }, 1000)
        },
        methods: {
            handleScroll() {
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
                const that = this;
                if (scrollTop <= 20) {
                    this.backToTopShow = true;
                } else {
                    this.backToTopShow = false;
                }
                this.itemList.forEach(el => {
                     //console.log(`scrollTop : ${scrollTop}`)
                     //console.log(`document.body.clientHeight : ${document.body.clientHeight}`)
                     //console.log(`el.offsetTop : ${el.offsetTop}`)
                     //console.log(`el.height : ${el.offsetHeight}`)


                    if (el.classList.contains('preview-session-flag')) {
                        let screenTop = Math.max(scrollTop - document.body.clientHeight / 2, 0);
                        let screenBottom = scrollTop + document.body.clientHeight * 1.5;

                        if (el.offsetTop > screenTop && el.offsetTop < screenBottom
                            || (el.offsetTop + el.offsetHeight) > screenTop && (el.offsetTop + el.offsetHeight) < screenBottom) {
                            that.showHeader = true;
                        } else {
                            that.showHeader = false;
                        }
                    }

                    if (el.classList.contains('service-map-flag')) {
                        let screenTop = Math.max(scrollTop - document.body.clientHeight / 2, 0);
                        let screenBottom = scrollTop + document.body.clientHeight * 1.5;

                        if (el.offsetTop > screenTop && el.offsetTop < screenBottom
                            || (el.offsetTop + el.offsetHeight) > screenTop && (el.offsetTop + el.offsetHeight) < screenBottom) {
                            that.showServiceMap = true;
                        } else {
                            that.showServiceMap = false;
                        }
                    }


                    if ((scrollTop + document.body.clientHeight) > el.offsetTop) {
                        // that.showFlagList[index + 1] = true
                        // TweenMax.fromTo(el, 3, {transform: 'translate3d(0, 50%, 0)', opacity: 0}, {transform: 'translate3d(0, 0, 0)', opacity: 1})
                        that.animateCSS(el, 'homeFadeInUp slower', function () {
                        });
                        // start the animation for operational view
                        if (el.classList.contains('operational') && (scrollTop + document.body.clientHeight) > el.offsetTop + el.offsetHeight) {
                            that.showOperationalData = true;
                        }
                    }
                });
            },
            backToTop() {
                // let nowTop = document.body.scrollTop || document.documentElement.scrollTop;
                // if (nowTop > 0) {
                //   window.scrollTo (0,nowTop - (nowTop/10));
                //   window.requestAnimationFrame(this.backToTop);
                // }
                window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
            },
            animateCSS(element, animationName, callback) {
                const node = element; //document.querySelector(element)
                if (node.classList.contains('animated')) {
                    return;
                }
                node.classList.add('animated', 'homeFadeInUp', 'content-show');
                node.classList.remove('content-hide');

                function handleAnimationEnd() {
                    if (typeof callback === 'function') callback();
                }

                node.addEventListener('animationend', handleAnimationEnd);
            }
        }
    };
</script>

