<template>
  <div class="home-item" :class="{'public-footer-container': isPc, 'public-footer-container-mb': !isPc}">
    <div class="content">
      <div class="js-introduce footer-item">
        <p class="title">{{$t('footer.business-name')}}</p>
        <p class="description">{{$t('footer.business-intro-line-1')}}</p>
        <p class="description">{{$t('footer.business-intro-line-2')}}</p>
        <p class="copy-right" v-if="isPc">{{$t('footer.copyright-line')}}</p>
      </div>
      <div class="footer-right">
        <div class="website-map footer-item">
          <p class="title">{{$t('footer.site-map')}}</p>
          <!--<a class="description link">{{$t('Footer.Paragraph-line-2-1')}}</a>
          <a class="description link">{{$t('Footer.Paragraph-line-2-2')}}</a>
          <a class="description link">{{$t('Footer.Paragraph-line-2-3')}}</a>-->
          <a class="description link" @click="goToMyService">{{$t('footer.site-map-item-2')}}</a>
          <a class="description link" @click="goToOrder">{{$t('footer.site-map-item-3')}}</a>
          <a class="description link" @click="goToHelp">{{$t('footer.site-map-item-4')}}</a>
          <a class="description link" @click="goToContactUs">{{$t('footer.site-map-item-5')}}</a>
        </div>
        <div class="contact-us footer-item">
          <p class="title">{{$t('footer.contact-us')}}</p>
          <p class="description call-center">{{$t('footer.contact-us-phone')}}</p>
          <p class="description">{{$t('footer.contact-us-phone-item-1')}}</p>
          <p class="description">{{$t('footer.contact-us-phone-item-2')}}</p>
          <p class="description email">{{$t('footer.contact-us-email')}}</p>
          <p class="description">{{$t('footer.contact-us-email-item-1')}}</p>
        </div>
      </div>
      <div v-if="!isPc" class="copy-right-container-mb">
        <p class="copy-right-mb">{{$t('footer.copyright-line')}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  import {AU_HMAC, AU_SELECTED_SERVICE, AU_UUID} from "@/requestServer";

  export default {
    name: 'PublicFooter',
    data() {
      return {
        isPc: this.$isPc()
      }
    },
    methods: {
      goToContactUs() {
        window.open(`${window.location.origin}/help/contact-us`,this.$inApp? '_self': '_blank');
      },
      goToMyService() {
        if (localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC)) {
            let id = localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)).id : 0 ;
            this.$router.push({
              path: `/user/service`,
              name: 'service',
              params: {
                serviceId: id,
              },
              query: this.$route.query
            });
        } else {
          this.$router.push({
            path: 'user/login',
            name: 'login',
            query: this.$route.query
          });
        }
      },
      goToOrder() {
        window.open(`${window.location.origin}/order/place`, this.$inApp ? '_self' : '_blank');
      },
      goToHelp() {
        window.open(`${window.location.origin}/help`, this.$inApp ? '_self' : '_blank');
      }
    }
  };
</script>

