<template>
  <div :class="{'auto-cover-flow-container': isPc, 'auto-cover-flow-container-mb': !isPc}">
    <!--<div class="cover-block" v-for="item of iconList">-->
    <div class="auto-cover-flow-content">
      <img class="cover-icon icon1" src="../assets/home/three-unlock-1.png" id="icon1"/>
      <img class="cover-icon icon2" src="../assets/home/three-unlock-2.png" id="icon2"/>
      <img class="cover-icon icon3" src="../assets/home/three-unlock-3.png" id="icon3"/>
    </div>
    <!--</div>-->
  </div>
</template>

<script>
  import {TimelineMax} from 'gsap';
  import {Power0} from 'gsap';
  import {Power4} from 'gsap';
  export default {
    name: 'AutoCoverFlowView',
    data() {
      return {
        indexLoop: 0,
        isPc: this.$isPc(),
        intervalId: 0
      };
    },
    mounted() {

      const that = this;
      this.intervalId = setInterval(function () {
        Array.prototype.forEach.call(document.getElementsByClassName('cover-icon'), function (el, index) {
          // Every loop need to switch the index, case the el is same one but the location is different
          switch (that.indexLoop % 3) {
            case 0:
              that.autoAction(el, index);
              break;
            case 1: {
              if (index + 1 > 2) {
                that.autoAction(el, 0);
              } else {
                that.autoAction(el, index + 1);
              }
            }
              break;
            case 2:
              if (index + 2 > 2) {
                that.autoAction(el, index - 1);
              } else {
                that.autoAction(el, index + 2);
              }
              break;
            default:
              break;
          }
        });

        that.indexLoop = that.indexLoop + 1;
      }, 4000);
    },
    beforeDestroy() {
      clearInterval(this.intervalId)
    },
    methods: {
      autoAction(el, index) {

        let tl = new TimelineMax({});

        switch (index) {
          case 0:
            tl.to(el, 0.1, {
              // .to(el, 3, {
              right: this.isPc ? '100px': '2.39rem',
              transform: 'scale(0.82) rotateY(0deg) translateZ(0px)',
              'z-index': 1,
              opacity: 0.8,
              ease: Power0.easeNone
            })
              .to(el, 0.4, {
                right: this.isPc ? '19px': '0.875rem',
                transform: 'scale(0.82) rotateY(0deg) translateZ(0px)',
                'z-index': 1,
                opacity: 0.8,
                ease: Power0.easeNone
              })
              .to(el, 0.4, {
                right: this.isPc ? '-62px': '-0.64rem',
                transform: 'scale(0.82) rotateY(0deg) translateZ(0px)',
                'z-index': 1,
                opacity: 0.8,
                ease: Power4.easeOut
              });
            break;
          case 1:
            tl.to(el, 0.2, {
              // .to(el, 3, {
              right: this.isPc ? '-62px': '-0.64rem',
              transform: 'scale(0.82) rotateY(0deg) translateZ(0px)',
              'z-index': 2,
              opacity: 0.8,
              ease: Power0.easeNone
            })
              .to(el, 0.4, {
                right: this.isPc ? '-21.5px': '0.1175rem',
                transform: 'scale(0.91) rotateY(-10deg) translateZ(40px)',
                'z-index': 10,
                opacity: 0.9,
                ease: Power0.easeNone
              })
              .to(el, 0.4, {
                right: this.isPc ? '19px': '0.875rem',
                transform: 'scale(1) rotateY(0deg) translateZ(80px)',
                'z-index': 10,
                opacity: 1,
                ease: Power4.easeOut
              });
            break;
          case 2:
            tl.to(el, 0.4, {
                right: this.isPc ? '59.5px': '1.6325rem',
                transform: 'scale(0.91) rotateY(10deg) translateZ(40px)',
                'z-index': 2,
                opacity: 0.9,
                ease: Power0.easeNone
              })
              .to(el, 0.4, {
                right: this.isPc ? '100px': '2.39rem',
                transform: 'scale(0.82) rotateY(0deg) translateZ(0px)',
                'z-index': 2,
                opacity: 0.8,
                ease: Power4.easeOut
              });
            break;
          default :
            break;
        }

      }
    }
  };
</script>

<style lang="scss">
  .auto-cover-flow-container {
    perspective: 900px;
    .auto-cover-flow-content {
      transform: rotateY(-8deg) rotateZ(2deg);
      .cover-icon {
        position: absolute;
        width: 405px;
        height: 426px;
        box-shadow: 8px 20px 50px rgba(26, 32, 60, 0.4);
        border-radius: 15px;
        /*animation: testCir2 24s linear infinite normal;*/
      }
      .icon1 {
        right: 100px;
        z-index: 1;
        opacity: 0.8;
        transform: scale(0.82);
        //background: url('../assets/home/three-unlock-1.png') center no-repeat;
      }
      .icon2 {
        right: -62px;
        z-index: 1;
        opacity: 0.8;
        transform: scale(0.82);
        //background: url('../assets/home/three-unlock-2.png') center no-repeat;
      }
      .icon3 {
        right: 19px;
        z-index: 10;
        opacity: 1;
        transform: scale(1);
        //background: url('../assets/home/three-unlock-3.png') center no-repeat;
      }
    }
  }

  .auto-cover-flow-container-mb {
    perspective: 900px;
    .auto-cover-flow-content {
      width: 8.933rem;
