      .cover-icon {
        position: absolute;
        width: 7.2rem;
        height: 7.573rem;
        box-shadow: 0.107rem 0.267rem 0.667rem rgba(26, 32, 60, 0.4);
        border-radius: 0.313rem;
        /*animation: testCir2 24s linear infinite normal;*/
      }
      .icon1 {
        right: 2.39rem;
        z-index: 1;
        opacity: 0.8;
        transform: scale(0.82);
        background: url('../assets/home/three-unlock-1.png') center no-repeat;
      }
      .icon2 {
        right: -0.64rem;
        z-index: 1;
        opacity: 0.8;
        transform: scale(0.82);
        background: url('../assets/home/three-unlock-2.png') center no-repeat;
      }
      .icon3 {
        right: 0.875rem;
